body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navBar ul{
    padding: 0;
    margin: 0;
    display: flex;
    list-style: none;
    width: 100%;
    background-color: #333;
    border: 5px solid #333333;
    
  }
  .navBar ul a{
    text-decoration: none;
    color: #999;
    display: block;
    padding: 15px;
    margin: 0 5px;
    
  }
  .navBar ul .active, .navBar ul a:hover{
    background: #FFF;
    color: #222;
  }
  .products{
    background-image:
    linear-gradient(
      skyblue, blue, white
    );
  }
  .contactUs{
    height:800px ;
    background: #228B22;
    color: #fff;
  }

  .aboutUs{
    height:800px ;
    background-image: url(/AAP/static/media/Areca_farm.443a24cf.jpg);
    background-repeat: no-repeat;
    background-size:100%;
    color: #228B22;
  }

  .card {
    background:#A0522D;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
    margin-left : auto;
    margin-right : auto;
    margin-top:10px;
  }
  .productCard {
    background:#222;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
    margin-left : auto;
    margin-right : auto;
    margin-top:10px;
  }
  .productCard > img{
    height:300px;
    width:350px;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .container {
    padding: 2px 16px;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

